#projects .project__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#projects .project__container .card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 70%;
  height: fit-content;
  border: 2px solid black;
}
#projects .project__container .card-container .card {
  width: 250px;
  height: 400px;
  text-align: center;
  margin: 1rem;
  display: flex;
  border: 2px solid grey;
  border-radius: 10px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
#projects .project__container .card-container .card img {
  width: 500px;
}
#projects .project__container .card-container .card p {
  display: none;
}
#projects .project__container .card-container .card .card:hover p {
  display: flex;
}