#projects {
    .project__container {
        display: flex;
        flex-direction: column;
        align-items: center;

        .card-container {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-evenly;
            width: 70%;
            height: fit-content;
            border: 2px solid black;

            .card {
                width: 250px;
                height: 400px;
                text-align: center;
                margin: 1rem;
                display: flex;
                border: 2px solid grey;
                border-radius: 10px;
                justify-content: center;
                flex-direction: column;
                align-items: center;

                img {
                    width: 500px;
                }

                p {
                    display: none;
                }

                .card:hover {
                    p {
                        display: flex;
                    }
                }
            }
        }
    }
}